body {
  background-color: black;
  font-family: "Roboto", sans-serif;
  padding: 0%;
  margin: 0;
}
* {
  transition: cubic-bezier(0.165, 0.84, 0.44, 1);
  -webkit-transition: cubic-bezier(0.165, 0.84, 0.44, 1);
  -moz-transition: cubic-bezier(0.165, 0.84, 0.44, 1);
  -ms-transition: cubic-bezier(0.165, 0.84, 0.44, 1);
  -o-transition: cubic-bezier(0.165, 0.84, 0.44, 1);
}

#parent {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  overflow: scroll;
}

#parent #main-video {
  /*Makevideotoatleast100%wideandtall*/
  min-width: 100%;
  background-color: black;
  min-height: 100%;
  /*Settingwidth&heighttoautopreventsthebrowserfromstretchingorsquishingthevideo*/
  width: auto;
  height: auto;
  /*Centerthevideo*/
  position: absolute;
  top: 30%;
  left: 50%;
  transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transform: translate(-50%, -30%);
  -webkit-transform: translate(-50%, -30%);
  -moz-transform: translate(-50%, -30%);
  -ms-transform: translate(-50%, -30%);
  -o-transform: translate(-50%, -30%);
  -webkit-transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -moz-transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -ms-transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
  -o-transition: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.little-parent {
  width: 150px;
  height: 150px;
  background-color: black;
  overflow: auto;
  position: relative;
  margin-left: 5px;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}

.little-parent #main-video {
  /*Makevideotoatleast100%wideandtall*/
  min-width: 100%;
  background-color: black;
  min-height: 100%;
  /*Settingwidth&heighttoautopreventsthebrowserfromstretchingorsquishingthevideo*/
  width: auto;
  height: auto;
  /*Centerthevideo*/
  position: absolute;
  cursor: pointer;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
}

.little-parent #main-video:hover {
  background-color: rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 10px rgba(255, 255, 255, 0.5);
}
